import './App.scss';
import React from 'react';
import todocontainer from './todo-container/todocontainer';

class App extends React.Component{

  constructor(props){
    super(props);
    this.state = {
      todos: [
        'Make Cofee',
        'Do Assignment'
      ]
    };
  }

    render(){
      return (
        <todocontainer todos={this.state.todos}></todocontainer>
        // <todoContainer todos={this.state.todos}></todoContainer>
      )
    }
}

export default App;
