import React from 'react';
import todoitem from './todo-item/todoitem';

class todocontainer extends React.Component {

    constructor(props){
        super(props);
    }

    render(){
        const todosList = this.props.todos.map(c => <todoitem todo={c}></todoitem>);
        return (
        <ul>
            {todosList}
        </ul>
        );
    }

}

export default todocontainer;